import { h, render, Component } from 'preact';
import { EVENT_QUOTES } from '@/utils/event-bus.js';
import { themeable } from '@/utils';
import SymbolRow from './row';

class Table extends Component {
    constructor (props) {
        super(props);

        this.state = {
            instruments: props.instruments
        };
    }

    updateInstrument (quote) {
        let { instruments } = this.state;
        let updated = 0;
        instruments = instruments.map((instrument) => {
            if (quote.s === instrument.symbol && Number(instrument.value) !== Number(quote.r)) {
                instrument.value = quote.r;
                instrument.percentChange = (((instrument.value * 100) / instrument.close) - 100).toFixed(2);
                updated++;
            }

            return instrument;
        });

        if (updated > 0) {
            this.setState({ instruments });
        }
    }

    componentDidMount () {
        this.props.bus.take(EVENT_QUOTES, (q) => {
            this.updateInstrument(q);
        });
    }

    render ({ bounds, theme }, { instruments = [] }) {
        return (
            <div className={ theme.table }>
                { instruments.map((instrument, index) => (
                    <SymbolRow {...instrument} {...bounds} className={index % 2 === 0 ? theme.odd : null} />
                )) }
            </div>
        );
    }
}

export default themeable(Table);



