'use strict';

import axios from 'axios';

const API_SERVER = 'https://gaterest.fxclub.com/real/restapi/quotes';
const request = axios.create({
    baseURL: API_SERVER
});

export function getCurrentQuotes (symbols, lang = 'eng') {
    let symbolsParams = symbols.join(',');
    return request.get('/currentquotes', {params: {symbols: symbolsParams}}).then((response) => {
        return response.data.Result.QuotesTrade;
    });
}



