import { h, render } from 'preact';
import { Measure, withContentRect } from 'react-measure';

import Dispatcher from '@/app/dispatcher';
import { ThemeProvider } from '@/utils';

// widgets list
import TableWidget from '@/components/table';

// themes list
import lightTheme from '@/styles/themes/light.css';
import darkTheme from '@/styles/themes/dark.css';

export default class ReactDispatcher extends Dispatcher {
    dispatch (requestedInstances) {
        const bus = this.app.getBus();
        requestedInstances.forEach(function (instance) {
            const { theme } = instance['config'];
            let instanceTheme;
            switch (theme) {
            case 'dark':
                instanceTheme = darkTheme;
                break;
            default:
                instanceTheme = lightTheme;
                break;
            }

            const WidgetMeasure = withContentRect('bounds')(({ measureRef, measure, contentRect }) => (
                <div ref={measureRef} className={instanceTheme.widgetContainer}>
                    <TableWidget bus={ bus } { ...instance['config'] } bounds={contentRect.bounds} />
                </div>
            ));

            render((
                <ThemeProvider theme={ instanceTheme }>
                    <WidgetMeasure />
                </ThemeProvider>
            ), instance['parent']);
        });
    }
}



