import { h, render, Component } from "preact";
import { getLink } from "@/utils/instruments";
import { themeable } from "@/utils";

import styles from "./style.css";

export class SymbolRow extends Component {
    shouldComponentUpdate(nextProps) {
        this.tickClass =
            this.props.value === nextProps.value
                ? ""
                : this.props.value > nextProps.value
                  ? this.props.theme["tickNeg"]
                  : this.props.theme["tickPos"];

        return (
            this.props.value !== nextProps.value ||
            this.props.width !== nextProps.width
        );
    }

    componentWillUpdate() {
        clearTimeout(this.stopAnimationTimer);
        if (this.tickClass.length) {
            this.animation.classList.add(this.tickClass);
            this.stopAnimationTimer = setTimeout(() => {
                this.animation.classList.remove(this.props.theme["tickPos"]);
                this.animation.classList.remove(this.props.theme["tickNeg"]);
            }, 500);
        }
    }

    render({
        alias,
        value,
        symbol,
        type,
        market,
        percentChange,
        width,
        className,
        theme
    }) {
        let rowrClassName = [styles.row, theme.tableRow];

        if (width > 480) {
            rowrClassName.push(styles.desktop);
        }

        if (className) {
            rowrClassName.push(className);
        }

        return (
            <div className={rowrClassName.join(" ")}>
                <div className={styles.name}>{alias}</div>
                <div className={styles.values}>
                    <div
                        className={styles.value}
                        ref={elm => {
                            this.animation = elm;
                        }}
                    >
                        {value}
                    </div>
                    <div
                        className={
                            percentChange > 0
                                ? `${styles.precent} ${theme.colorPositive}`
                                : `${styles.precent} ${theme.colorNegative}`
                        }
                    >
                        {percentChange}%
                    </div>
                </div>
                <div className={styles.buttons}>
                    <a
                        href={getLink({ market, type, symbol })}
                        target="_blank"
                        className={theme.btnUp}
                    >
                        Buys
                    </a>
                    <a
                        href={getLink({ market, type, symbol })}
                        target="_blank"
                        className={theme.btnDown}
                    >
                        Sell
                    </a>
                </div>
            </div>
        );
    }
}

export default themeable(SymbolRow);



