'use strict';

import axios from 'axios';

const API_SERVER = 'https://widget-libertex-zhilyaev.web2.dev.fxclub.org/api';
let request = axios.create({
    baseURL: API_SERVER
});

export function getInstruments (symbols, lang = 'eng') {
    let symbolsParams = symbols.join(',');
    return request.get(`/init/${lang}/${symbolsParams}`).then((response) => {
        return response.data.result;
    });
}



