import { getInstruments } from '@/api/web-sdk';
import { getCurrentQuotes } from '@/api/gaterest-sdk';

/**
 * Builds objects for specified instruments
 * @param  {array} symbols An array of instrument symbol
 * @return {object} Returns a mapped object where
 * @example
 *     [
 *         {
 *             value: 1.23,         - the current value of instrument
 *             percentChange: 0.54, -
 *             symbol:"PA",         -
 *             alias: "Palladium",  -
 *             type":"future",      -
 *             "market":"Metals",   -
 *             "open":988.15,       -
 *             "high":995.37,       -
 *             "low":985.9,         -
 *             "close":993.52,      -
 *             "numDigit":2         -
 *          }
 *    ]
 */
export async function factory (symbols) {
    const currentQuotes = await getCurrentQuotes(symbols);
    const instruments = await getInstruments(symbols);
    return currentQuotes.map(q => {
        const info = instruments[q.s] || {};
        return {
            value: q.r,
            percentChange: (q.r * 100 / info['close'] - 100).toFixed(2),
            ...info
        };
    });
};

/**
 * Build url to instrument datail page.
 *
 *
 * @param  {object} instrument An instrument object.
 * @return {string} The instrument home page.
 */
export function getLink ({symbol, market, type}) {
    return '';
}



