'use strict';

import { createBus } from 'suber';

import { extractWidgetConfig } from '@/utils/config';
import { factory as instrumentsFactory } from '@/utils/instruments';
import ReactDispatcher from '@/app/dispatcher/react';
import WsGate from '@/api/wsgate';

let appConfig = {};
let wconfig = {};
let lang = null;
let symbols = [];
let widgets = [];

const DEFAULT_LANG = 'en';
const bus = createBus();

function getWidgetsConfig (instances) {
    let widgets = [];
    instances.forEach((inst) => {
        let parent;
        if (!inst.hasOwnProperty('parent') ||
        !(parent = document.querySelector(inst['parent']))
        ) {
            console.debug('Skipped instance. The parent is invalid');
            return;
        }

        if (!inst.hasOwnProperty('config') ||
        !inst['config'].hasOwnProperty('symbols') ||
        !Array.isArray(inst['config']['symbols']) ||
        !inst['config']['symbols'].length === 0
        ) {
            console.debug('Skipped instance. The symbols is invalid');
            return;
        }

        inst['parent'] = parent;
        inst['config']['width'] = parent.offsetWidth;
        inst['config']['height'] = parent.offsetHeight;
        widgets.push(inst);
    });

    return widgets;
}

function getAllInstrumentSymbols (widgetsConfig) {
    let symbols = [];
    (widgetsConfig || []).forEach(function (widget, index) {
        symbols = symbols.concat(widget['config']['symbols']);
    });
    return symbols.filter((v, i, a) => a.indexOf(v) === i);
}

export default class Application {
    constructor (config) {
        wconfig = extractWidgetConfig(config['globalScope'])['config'];
        appConfig = config;
    }

    bootstrap () {
        if (!wconfig.hasOwnProperty('instances') || !Array.isArray(wconfig['instances'])) {
            throw new Error('The instances attribute is invalid');
        }

        lang = wconfig['lang'] || DEFAULT_LANG;
        widgets = getWidgetsConfig(wconfig['instances']);
        symbols = getAllInstrumentSymbols(widgets);

        return this;
    }

    run () {
        let self = this;
        instrumentsFactory(symbols).then((instruments) => {
            widgets.forEach((w) => {
                let { symbols } = w['config'];
                w['config']['instruments'] = instruments.filter(instrument => {
                    return symbols.indexOf(instrument.symbol) !== -1;
                });
            });

            const dispatcher = new ReactDispatcher(self);
            dispatcher.dispatch(widgets);
        });

        let gate = new WsGate(this.getOption('ws'), bus);
        gate.open().then((api) => {
            api.subscribe(symbols);
        });

        return this;
    }

    /**
     * Returns application data bus
     * @return {object}
     */
    getBus () {
        return bus;
    }

    /**
     * The application configuration option by name
     *
     * @param  {string} name
     * @param  {mixen} defaultValue
     * @return {mixen}
     */
    getOption (name, defaultValue = null) {
        return appConfig[lang][name] || appConfig[name] || defaultValue;
    }
}



